import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { PeriodoAcademicoModel } from '../../models/aerc-models/periodo-academico-model';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';

@Injectable({
  providedIn: 'root'
})
export class PeriodoAcademicoService {


  private urlBase = `${environment.backend.aerc}api/periodo`;
  constructor(
    private http: HttpClient,
  ) { }

  public findAll(): Observable<Array<PeriodoAcademicoModel>> {
    return this.http.get<Array<PeriodoAcademicoModel>>(`${this.urlBase}/find-all`);
  }

  public savePeriodo(periodo: PeriodoAcademicoModel): Observable<PeriodoAcademicoModel> {
    return this.http.post<PeriodoAcademicoModel>(`${this.urlBase}/save`, periodo);
  }

}
