import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { CalificacionModel } from '../models/gestion-academica/calificacion.model';
@Injectable({
    providedIn: 'root'
})
export class CalificacionService {

    constructor(
        private http: HttpClient,
    ) { }

    public getCalificaciones(): Observable<CalificacionModel[]> {
        return this.http.get<CalificacionModel[]>(`${environment.backend.gestion_academica}api/calificacion/v1/get-all`);
    }

}