import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { EstadoModel } from '../models/gestion-academica/estado.model';
import { EvaluacionCompromisoModel } from '../models/gestion-academica/evaluacion-compromiso.model';
@Injectable({
    providedIn: 'root'
})
export class EvaluacionCompromisoService {

    constructor(
        private http: HttpClient,
    ) { }

    public save(evaluacion: EvaluacionCompromisoModel): Observable<EvaluacionCompromisoModel> {
        return this.http.post<EvaluacionCompromisoModel>(`${environment.backend.gestion_academica}api/evaluacion-compromiso`, evaluacion);
    }

    public getEvaluacionByCompromiso(compromiso: number): Observable<EvaluacionCompromisoModel> {
        return this.http.get<EvaluacionCompromisoModel>(`${environment.backend.gestion_academica}api/evaluacion-compromiso/v1/get-by-compromiso/${compromiso}`);
    }

}