import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { TipoAcademicoModel } from '../models/gestion-academica/tipo-academico.model';
@Injectable({
    providedIn: 'root'
})
export class TipoAcademicoService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllTipoAcademico(): Observable<TipoAcademicoModel[]> {
        return this.http.get<TipoAcademicoModel[]>(`${environment.backend.gestion_academica}api/tipo-academico/v1/get-all`);
    }

}