import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';

@Injectable({
  providedIn: 'root'
})
export class TipoSolicitudService {


  private urlBase = `${environment.backend.aerc}api/tipo-solicitud`;
  constructor(
    private http: HttpClient,
  ) { }

  public getAllTipoSolicitud(): Observable<Array<TipoSolicitudModel>> {
    return this.http.get<Array<TipoSolicitudModel>>(`${this.urlBase}/find-all`);
  }

  public getAllTipoSolicitudAdmin(): Observable<Array<TipoSolicitudModel>> {
    return this.http.get<Array<TipoSolicitudModel>>(`${this.urlBase}/find-all-admin`);
  }

  public saveTipoSolicitud(tipoSolicitud: TipoSolicitudModel): Observable<boolean> {
    return this.http.post<boolean>(`${this.urlBase}/change-estado`, tipoSolicitud);
  }

}
