import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { FacultadModel } from '../models/gestion-academica/facultad.model';
import { environment } from 'src/environments/environment';
@Injectable({
    providedIn: 'root'
})
export class FacultadService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllFacultades(): Observable<Array<FacultadModel>> {
        return this.http.get<Array<FacultadModel>>(`${environment.backend.gestion_academica}api/facultad/v1/get-all`);
    }

}