import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { JerarquiaModel } from '../models/gestion-academica/jerarquia.model';
import { JerarquiaActualModel } from '../models/gestion-academica/jerarquia-actual.model';
@Injectable({
    providedIn: 'root'
})
export class JerarquiaService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllJerarquia(): Observable<JerarquiaModel[]> {
        return this.http.get<JerarquiaModel[]>(`${environment.backend.gestion_academica}api/jerarquia/v1/get-all`);
    }

    public getJerarquiaActual(rut: string): Observable<JerarquiaActualModel> {
        return this.http.get<JerarquiaActualModel>(`${environment.backend.gestion_academica}api/jerarquia/v1/get-jerarquia-actual-docente/${rut}`);
    }

    public getJerarquiaActualMasivo(ruts: string[]): Observable<JerarquiaActualModel[]> {
        return this.http.post<JerarquiaActualModel[]>(`${environment.backend.gestion_academica}api/jerarquia/v1/get-jerarquia-actual-docente-masivo`, ruts);
    }

}
