import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { AprobadorAercModel } from '../../models/aerc-models/aprobador-aerc.model';
import { DimensionModel } from '../../models/evaluacion-docente/mantenedores/dimensiones.model';
import { PreguntaModel } from '../../models/evaluacion-docente/mantenedores/preguntas.model';
import { PeriodoModel } from '../../models/evaluacion-docente/mantenedores/periodos.model';
import { TipoRespuestaModel } from '../../models/evaluacion-docente/mantenedores/tipo-respuesta.model';
import { TipoRespuestaConfiguracionModel } from '../../models/evaluacion-docente/mantenedores/tipo-respuesta-configuracion.model';
import { InstrumentoModel } from '../../models/evaluacion-docente/mantenedores/instrumentos.model';

@Injectable({
  providedIn: 'root'
})
export class MantenedoresEvaluacionDocenteService {


  private urlBase = `${environment.backend.evaluacion_docente}api`;
  constructor(
    private http: HttpClient,
  ) { }


  //** DIMENSIONES */

  public listarDimensiones(): Observable<Array<DimensionModel>> {
    return this.http.get<Array<DimensionModel>>(`${this.urlBase}/dimension/find-all`);
  }

  public saveDimension(dimension: DimensionModel): Observable<DimensionModel> {
    return this.http.post<DimensionModel>(`${this.urlBase}/dimension/save`, dimension);
  }

  //** PREGUNTAS */
  
  public paginationPreguntas(searchPagination: SearchPagination<string>, dimension: string): Observable<IPaginacion<PreguntaModel>> {
    return this.http.post<IPaginacion<PreguntaModel>>(`${this.urlBase}/pregunta/pagination?dimension=${dimension}`, searchPagination);
  }

  public savePregunta(pregunta: PreguntaModel): Observable<PreguntaModel> {
    return this.http.post<PreguntaModel>(`${this.urlBase}/pregunta/save`, pregunta);
  }

  public updatePregunta(pregunta: PreguntaModel): Observable<PreguntaModel> {
    return this.http.post<PreguntaModel>(`${this.urlBase}/pregunta/update`, pregunta);
  }

  //** PERIODOS */

  public paginationPeriodos(searchPagination: SearchPagination<string>): Observable<IPaginacion<PeriodoModel>> {
    return this.http.post<IPaginacion<PeriodoModel>>(`${this.urlBase}/periodo/pagination`, searchPagination);
  }

  public savePeriodo(periodo: PeriodoModel): Observable<PeriodoModel> {
    return this.http.post<PeriodoModel>(`${this.urlBase}/periodo/save`, periodo);
  }

  public updatePeriodo(periodo: PeriodoModel): Observable<PeriodoModel> {
    return this.http.post<PeriodoModel>(`${this.urlBase}/periodo/update`, periodo);
  }


  //** CONFIGURACIÃ“N DE RESPUESTAS */

  public paginationTipoRespuestas(searchPagination: SearchPagination<string>): Observable<IPaginacion<TipoRespuestaModel>> {
    return this.http.post<IPaginacion<TipoRespuestaModel>>(`${this.urlBase}/tipo-respuesta/pagination`, searchPagination);
  }

  public saveTipoRespuesta(tipoRespuesta: TipoRespuestaModel): Observable<TipoRespuestaModel> {
    return this.http.post<TipoRespuestaModel>(`${this.urlBase}/tipo-respuesta/save`, tipoRespuesta);
  }

  public updateTipoRespuesta(tipoRespuesta: TipoRespuestaModel): Observable<TipoRespuestaModel> {
    return this.http.post<TipoRespuestaModel>(`${this.urlBase}/tipo-respuesta/update`, tipoRespuesta);
  }

  public getConfiguracionTipoRespuesta(codigo: string): Observable<Array<TipoRespuestaConfiguracionModel>> {
    return this.http.get<Array<TipoRespuestaConfiguracionModel>>(`${this.urlBase}/tipo-respuesta-configuracion/find-by-codigo/${codigo}`);
  }

  public saveAllTipoRespuestaConfiguracion(configTipoRespuesta: Array<TipoRespuestaConfiguracionModel>): Observable<Array<TipoRespuestaConfiguracionModel>> {
    return this.http.post<Array<TipoRespuestaConfiguracionModel>>(`${this.urlBase}/tipo-respuesta-configuracion/save-all`, configTipoRespuesta);
  }

  //** INSTRUMENTOS */

  public paginationInstrumentos(searchPagination: SearchPagination<string>): Observable<IPaginacion<InstrumentoModel>> {
    return this.http.post<IPaginacion<InstrumentoModel>>(`${this.urlBase}/instrumento/pagination`, searchPagination);
  }

  public saveInstrumento(instrumento: InstrumentoModel): Observable<InstrumentoModel> {
    return this.http.post<InstrumentoModel>(`${this.urlBase}/instrumento/save`, instrumento);
  }

  public updateInstrumento(instrumento: InstrumentoModel): Observable<InstrumentoModel> {
    return this.http.post<InstrumentoModel>(`${this.urlBase}/instrumento/update`, instrumento);
  }

  public duplicarInstrumento(instrumento: InstrumentoModel, codigo: string): Observable<InstrumentoModel> {
    return this.http.post<InstrumentoModel>(`${this.urlBase}/instrumento/duplicar-instrumento?codigoInstrumento=${codigo}`, instrumento);
  }

}
