import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { DecanoModel } from '../models/gestion-academica/decano.model';
import { ArchivoEvidenciaModel } from '../models/gestion-academica/archivo-evidencia.model';

@Injectable({
    providedIn: 'root'
})
export class ArchivoEvidenciaService {

    constructor(
        private http: HttpClient,
    ) { }
    public save(archivo: ArchivoEvidenciaModel[]): Observable<ArchivoEvidenciaModel[]> {
        return this.http.post<ArchivoEvidenciaModel[]>(`${environment.backend.gestion_academica}api/archivo/v1`, archivo);

    }

    public getArchivoByEvaluacion(evaluacion: number): Observable<ArchivoEvidenciaModel[]> {
        return this.http.get<ArchivoEvidenciaModel[]>(`${environment.backend.gestion_academica}api/archivo/v1/get-by-evaluacion/${evaluacion}`);
    }


}