import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { FuncionModel } from '../models/gestion-academica/funcion.model';
@Injectable({
    providedIn: 'root'
})
export class FuncionService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllFuncion(): Observable<FuncionModel[]> {
        return this.http.get<FuncionModel[]>(`${environment.backend.gestion_academica}api/funcion/v1/get-all`);
    }

}