import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { EvaluacionVcmDetalleModel } from '../models/gestion-academica/evaluacion-vcm.model';
@Injectable({
    providedIn: 'root'
})
export class EvaluacionCompromisoVcmService {

    constructor(
        private http: HttpClient,
    ) { }

    public save(respuestas: EvaluacionVcmDetalleModel[]): Observable<EvaluacionVcmDetalleModel[]> {
        return this.http.post<EvaluacionVcmDetalleModel[]>(`${environment.backend.gestion_academica}api/evaluacion-detalle-vcm`, respuestas);
    }

    public getEvaluacionVcmByEvaluacion(evaluacion: number): Observable<EvaluacionVcmDetalleModel[]> {
        return this.http.get<EvaluacionVcmDetalleModel[]>(`${environment.backend.gestion_academica}api/evaluacion-detalle-vcm/v1/get-by-evaluacion/${evaluacion}`);
    }
}