import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { DocenteModel } from '../models/gestion-academica/docente.model';
import { PublicacionDocenteModel } from '../models/gestion-academica/publicacion-docente.model';
import { DifusionDocenteModel } from '../models/gestion-academica/difusion-docente.model';
import { LibroCapituloDocenteModel } from '../models/gestion-academica/librocapitulo-docente.model-';
import { AdjudicacionDocenteModel } from '../models/gestion-academica/adjudicacion-docente.model';
import { MateriaModel } from '../models/gestion-academica/materia.model';
import { EncuestaDocenteModel } from '../models/gestion-academica/encuesta-docente.model';
@Injectable({
    providedIn: 'root'
})
export class DocenteService {

    constructor(
        private http: HttpClient,
    ) { }

    public getDocenteBannerById(rut: string): Observable<DocenteModel> {
        return this.http.get<DocenteModel>(`${environment.backend.gestion_academica}api/docente/get-datos-by-rut/${rut}`);
    }

    public getPublicacionesByRutAndAnio(rut: string, anio: number, tipo: number, variacion: number): Observable<PublicacionDocenteModel[]> {
        return this.http.get<PublicacionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-publicaciones-cud/${anio}/${rut}/${tipo}/${variacion}`);
    }
    public getDifusionesByRutAndAnio(rut: string, anio: number, tipo: number, variacion: number): Observable<DifusionDocenteModel[]> {
        return this.http.get<DifusionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-difusiones-cud/${anio}/${rut}/${tipo}/${variacion}`);
    }
    public getLibroCapituloByRutAndAnio(rut: string, anio: number, tipo: number, variacion: number): Observable<LibroCapituloDocenteModel[]> {
        return this.http.get<LibroCapituloDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-libro-capitulos-cud/${anio}/${rut}/${tipo}/${variacion}`);
    }
    public getAdjudicacionByRutAndAnio(rut: string, anio: number, tipo: number, variacion: number): Observable<AdjudicacionDocenteModel[]> {
        return this.http.get<AdjudicacionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/adjudicacion-cud/${anio}/${rut}/${tipo}/${variacion}`);
    }
    public getAdjudicacionByRutAndAnioVcM(rut: string, anio: number, tipo: number, variacion: number): Observable<AdjudicacionDocenteModel[]> {
        return this.http.get<AdjudicacionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/adjudicacion-cud-vcm/${anio}/${rut}/${tipo}/${variacion}`);
    }

    public getHistoricoMaterias(rut: string, tipo: string): Observable<MateriaModel[]> {
        return this.http.get<MateriaModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-materias-by-docente/${rut}/${tipo}`);
    }

    public findEncuestaDocente(rut: string, periodo: string, codigoCurso): Observable<Array<EncuestaDocenteModel>> {
        return this.http.get<Array<EncuestaDocenteModel>>(`${environment.backend.gestion_academica}api/docente/v1/get-encuesta-docente/${rut}/${periodo}/${codigoCurso}`);
    }

    public getPublicacionesByDocentes(rut: String[], anio: String): Observable<PublicacionDocenteModel[]> {
        return this.http.post<PublicacionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-publicaciones-cud/${anio}`, rut);
    }
    public getAdjudicacionesByDocentes(rut: String[], anio: String): Observable<AdjudicacionDocenteModel[]> {
        return this.http.post<AdjudicacionDocenteModel[]>(`${environment.backend.gestion_academica}api/docente/v1/get-adjudicaciones-cud/${anio}`, rut);
    }
}
