import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { EstadoModel } from '../models/gestion-academica/estado.model';
@Injectable({
    providedIn: 'root'
})
export class EstadoService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllEstado(): Observable<EstadoModel[]> {
        return this.http.get<EstadoModel[]>(`${environment.backend.gestion_academica}api/estado/v1/get-all`);
    }

}