import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { EstadoModel } from '../models/gestion-academica/estado.model';
import { EvaluacionCompromisoModel } from '../models/gestion-academica/evaluacion-compromiso.model';
import { EvaluacionDetalleModel } from '../models/gestion-academica/evaluacion-detalle.model';
@Injectable({
    providedIn: 'root'
})
export class EvaluacionCompromisoDetalleService {

    constructor(
        private http: HttpClient,
    ) { }

    public save(respuestas: EvaluacionDetalleModel[]): Observable<EvaluacionDetalleModel[]> {
        return this.http.post<EvaluacionDetalleModel[]>(`${environment.backend.gestion_academica}api/evaluacion-detalle`, respuestas);
    }

    public getEvaluacionDetalleByEvaluacion(evaluacion: number): Observable<EvaluacionDetalleModel[]> {
        return this.http.get<EvaluacionDetalleModel[]>(`${environment.backend.gestion_academica}api/evaluacion-detalle/v1/get-by-evaluacion/${evaluacion}`);
    }

}